*{
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 50px;
}

input{
  color: grey; 
  font-size: 1.5rem;
  width: 100%;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;


}

.card p {
  padding: 170;
  text-align: center;

}

.card img{
  max-width: 170%;
}